const menu = document.querySelector(`.main-menu`)
const subMenus = menu.querySelectorAll(`.main-menu__sub-menu`)

for (const subMenu of subMenus) {
  const dropBtn = subMenu.parentElement
  dropBtn.addEventListener(`click`, () => {
    subMenu.classList.toggle(`shown`)
  })
  dropBtn.addEventListener('keydown', (e) => {
    if (e.key === 'Enter' || e.key === ' ') {
      subMenu.classList.toggle('shown')
    }
  })
}

document.addEventListener('DOMContentLoaded', function() {
  const currentUrl = window.location.pathname;

  const menuItems = menu.querySelectorAll('[class*="item"]');

  menuItems.forEach(function(item) {
    if (item.children[0].getAttribute('href') === currentUrl) {
      item.classList.add('current-page');
    }
  });
});
